import request from '../utils/http'

//查找全部/部分api
export function getData (payload) {
  return request({
    url: 'api/get',
    method: 'GET',
    params:payload
  })
}
//获取/查找角色
export function getroleData (payload) {
  return request({
    url: 'role/get',
    method: 'GET',
    params:payload
  })
}
//获取 用户
export function getuserData (payload) {
  return request({
    url: 'user/get',
    method: 'GET',
    params:payload
  })
}
//新增api
export function addApi (payload) {
  return request({
    url: 'api/create',
    method: 'POST',
    data:payload
  })
}
//删除api
export function deleteApi (params) {
  return request({
    url: 'api/delete',
    method: 'DELETE',
    params:params
  })
}
//修改api
export function updateApi (payload) {
  return request({
    url: 'api/update',
    method: 'PUT',
    data:payload
  })
}
//新增角色
export function createUser (payload) {
  return request({
    url: 'user/create',
    method: 'POST',
    params:payload
  })
}
//修改用户{暂时没写，不能修改}

//删除用户
export function deleteUser (params) {
  return request({
    url: 'user/delete',
    method: 'DELETE',
    params:params
  })
}


