<template>
  <div class="userManagement-body">
    <div class="userManagement-above">
      <div class="userManagement-above-body">
        <span> 角色名 </span>
        <div class="userManagement-above-body-input">
          <el-input
            v-model="input"
            placeholder="请输入角色名称"
            maxlength="30px"
          />
        </div>
        <div class="userManagement-above-body-button">
          <el-button type="primary" @click="searchRole(input)">查询</el-button>
        </div>
      </div>
    </div>
    <div class="userManagement-under">
      <div class="userManagement-under-body">
        <div class="userManagement-under-body-button">
          <el-button type="primary" @click="addUser()">新增</el-button>
          <el-dialog v-model="dialogFormVisible2" title="新增用户">
            <el-form :model="userForm">
              <el-form-item label="账号">
                <el-input v-model="userForm.Name" autocomplete="off" />
              </el-form-item>
              <el-form-item label="用户名">
                <el-input v-model="userForm.Account" autocomplete="off" />
              </el-form-item>
              <el-form-item label="电话">
                <el-input v-model="userForm.Phone" autocomplete="off" />
              </el-form-item>
              <el-form-item label="性别">
                <el-radio-group v-model="userForm.Sex">
                  <el-radio :label="0" value="男">男</el-radio>
                  <el-radio :label="1" value="女">女</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="密码 ">
                <el-input v-model="userForm.Password" autocomplete="off" />
              </el-form-item>
              <el-form-item label="请选择">
                <el-select
                  v-model="userForm._RoleId"
                  placeholder="Please select a zone"
                >
                  <el-option
                    label="超级管理员"
                    value="647430fab59debc6e23018c9"
                  />
                </el-select>
              </el-form-item>
            </el-form>
            <template #footer>
              <span class="dialog-footer">
                <el-button @click="closeWindow()">取消</el-button>
                <el-button type="primary" @click="addUser1(userForm)">
                  确认
                </el-button>
              </span>
            </template>
          </el-dialog>
        </div>
        <div class="userManagement-under-body-table">
          <el-table :data="userTable" style="width: 100%" :header-cell-style="{textAlign: 'center'}">
            <el-table-column prop="AvatarUrl" label="头像" width="180" align="center">
              <template #default="scope1">
                <el-image :src="userTable[scope1.$index].AvatarUrl"></el-image>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="账号" width="180" align="center"  />
            <el-table-column prop="account" label="用户名" align="center" />
            <el-table-column prop="phone" label="电话" align="center" />
            <el-table-column prop="sex" label="性别" align="center" />
            <el-table-column prop="roleId" label="角色" align="center">
              <template #default="scope2">
                <div
                  v-if="
                    userTable[scope2.$index].roleId ==
                    '647430fab59debc6e23018c9'
                  "
                  class="userManagement-under-body-table-role"
                >
                  超级管理员
                </div >
                <div v-if="
                    userTable[scope2.$index].roleId !=
                    '647430fab59debc6e23018c9'
                  ">普通人</div>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="scope">
                <span
                  style="font-size: 10px; color: #4d70ff; cursor: pointer"
                  @click="getScope(scope.$index)"
                  >操作
                </span>
                <el-dialog
                  v-model="dialogFormVisible3"
                  title="Shipping address"
                >
                  <el-form :="updateUserForm">
                    <el-form-item label="账号">
                      <el-input
                        v-mode="updateUserForm.Name"
                        autocomplete="off"
                      />
                    </el-form-item>
                    <el-form-item label="用户名">
                      <el-input
                        v-model="updateUserForm.Account"
                        autocomplete="off"
                      />
                    </el-form-item>
                    <el-form-item label="电话">
                      <el-input
                        v-model="updateUserForm.Phone"
                        autocomplete="off"
                      />
                    </el-form-item>
                  </el-form>
                  <template #footer>
                    <span class="dialog-footer">
                      <el-button @click="dialogFormVisible3 = false"
                        >取消</el-button
                      >
                      <el-button type="primary" @click="addUser1(userForm)">
                        确认
                      </el-button>
                    </span>
                  </template>
                </el-dialog>
                <span
                  style="
                    font-size: 10px;
                    margin-left: 10px;
                    color: #4d70ff;
                    cursor: pointer;
                  "
                  @click="deleRow(scope.$index)"
                  >删除</span
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="userManagement-under-body-pagination">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            @current-change="paging"
          />
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { ref, reactive, onMounted } from "vue";
import { getuserData } from "@/api/authorityCenter.js";
import { createUser } from "@/api/authorityCenter.js";
import { ElMessage } from "element-plus";
import { deleteUser } from "@/api/authorityCenter.js";
export default {
  setup() {
    let input = ref("");
    let value = ref("");
    let dialogFormVisible2 = ref(false);
    let dialogFormVisible3 = ref(false);
    let userForm = reactive({
      AvatarUrl: "",
      Name: "",
      Account: "",
      Phone: "",
      Sex: "男",
      Password: "",
      _RoleId: "",
    });
    let updateUserForm = ref({});
    const payload = {
      currPage: "1",
      pageSize: "9",
    };
    const options = [
      {
        value: "Option1",
        label: "Option1",
      },
      {
        value: "Option2",
        label: "Option2",
      },
      {
        value: "Option3",
        label: "Option3",
      },
      {
        value: "Option4",
        label: "Option4",
      },
      {
        value: "Option5",
        label: "Option5",
      },
    ];
    let userTable = ref([""]);
    let total = ref("0");
    function getuserData1(payload) {
      getuserData(payload).then((res) => {
        console.log(res);
        userTable.value = res.data.data.pageData;
        total.value = Number(res.data.data.total);
      });
    }
    function paging(val) {
      let payload2 = reactive({
        currPage: val,
        pageSize: "9",
      });
      getuserData(payload2).then((res) => {
        userTable.value = res.data.data.pageData;
      });
    }
    function getSex(abc) {
      console.log(abc.sex);
      if (abc.Sex == 0) {
        abc.Sex = "男";
      } else if (abc.Sex == 1) {
        abc.Sex = "女";
      }
    }
    function addUser() {
      dialogFormVisible2.value = true;
      console.log(userTable.value[0]);
    }
    function addUser1(ghj) {
      getSex(ghj);
      createUser(ghj).then((res) => {
        if (res.data.code == 200) {
          ElMessage({
            message: "添加成功",
            type: "success",
          });
          getuserData1(payload);
        } else {
          ElMessage({
            message: "添加失败",
            type: "error",
          });
        }
      });
      dialogFormVisible2.value = false;
      clear();
    }
    let src1 = ref("");
    function getScope1(scope1) {
      src1.value = userTable.value[scope1].AvatarUrl;
      console.log(src1.value);
    }
    // setTimeout(() => {
    //   getScope1(scope1)
    // }, 1000);
    function getScope(ppp) {
      dialogFormVisible3.value = true;
      console.log(userTable.value[ppp]);
      let updateUserForm1 = {
        Name: userTable.value[ppp].name,
        Account: userTable.value[ppp].account,
        Phone: userTable.value[ppp].phone,
        Sex: userTable.value[ppp].sex,
        _RoleId: userTable.value[ppp].roleId,
      };
      updateUserForm.value = updateUserForm1;
      console.log(updateUserForm1);
    }
    function deleRow(jjj) {
      let params = {
        _id: userTable.value[jjj]._id,
      };
      deleteUser(params).then((res) => {
        if (res.data.code == 200) {
          ElMessage({
            message: "删除成功",
            type: "success",
          });
          getuserData1(payload);
        } else {
          ElMessage({
            message: "删除失败",
            type: "error",
          });
        }
        dialogFormVisible3.value = false;
      });
    }
    function searchRole(abc) {
      let payload = {
        currPage: "1",
        pageSize: "9",
        name: abc,
      };
      getuserData(payload).then((res) => {
        if (res.data.code == 200) {
          userTable.value = res.data.data.pageData;
          total.value = Number(res.data.data.total);
        } else {
          ElMessage({
            message: "查询失败",
            type: "error",
          });
        }
      });
    }
    function clear() {
      (userForm.Name = ""),
        (userForm.Account = ""),
        (userForm.Phone = ""),
        (userForm.Sex = "男"),
        (userForm._RoleId = ""),
        (userForm.Password = "");
    }
    function closeWindow() {
      clear();
      dialogFormVisible2.value = false;
    }
    onMounted(() => {
      getuserData1(payload);
    });
    return {
      input,
      value,
      options,
      userTable,
      payload,
      total,
      dialogFormVisible2,
      userForm,
      updateUserForm,
      dialogFormVisible3,
      src1,
      getuserData1,
      paging,
      addUser,
      addUser1,
      deleRow,
      getSex,
      getScope,
      searchRole,
      closeWindow,
      clear,
      getScope1,
    };
  },
};
</script>
  
  <style scoped>
.userManagement-body {
  width: 100%;
  height: 100%;
}
.userManagement-above {
  position: absolute;
  width: 85%;
  height: 13%;
  background-color: white;
  margin-top: -2%;
}
.userManagement-above-body {
  font-size: 15px;
  padding: 3%;
  margin-left: 3%;
  display: flex;
}
.userManagement-above-body-input {
  display: inline;
  width: 20%;
  margin-left: 1%;
  margin-top: -0.3%;
}
.userManagement-above-body-button {
  margin-left: 1%;
  margin-top: -0.3%;
}
.userManagement-under {
  position: absolute;
  background-color: white;
  width: 85%;
  height: 70%;
  margin-top: 5%;
}
.userManagement-under-body {
  display: flex;
  padding: 1%;
}
.userManagement-under-body-button {
  display: inline;
}
.userManagement-under-body-table {
  margin-left: -5%;
  margin-top: 4%;
  width: 100%;
  height: 80%;
  max-height: 80%;
  overflow: hidden;
}
.userManagement-under-body-pagination {
  display: inline;
  margin-top: 37%;
  margin-left: -65%;
}
.userManagement-under-body-table-role{
  border: 1px  solid rgb(14, 211, 14);
  text-align: center;
  border-radius: 5px;
  color: rgb(14, 211, 14);
}
</style>